/**
 * authweilan
 * 
 */
import router from "@/router/index";
import nextRouter from "@/router/next-router"; // 
import { flattenDeep } from '@/util/array'; // 
import store from "@/store";

/**
 * 
 * @param {*} data 
 */
function addUserRouter(data) {
  // 
  let userRouter = {
    path: "/layout",
    name: "layout",
    component: () => import('@/views/layout/index.vue'),
    redirect: '/index',
    children: []
  };
  // 
  let routerBox = [];
  // 
  let menu = flattenDeep(data, 'children');
  //  
  menu.forEach(item => {
    if (!item.url) return;
    try {
      let routerItem = {
        icon:item.icon,
        path: item.url, // 
        name: item.id, //  
        meta: {
          purview: item.permissions,
          code: item.code,
        }, //  
        component: () => import(`@/views${item.url}/index.vue`) // 
      };
      routerBox.push(routerItem);
    } catch (err) {
      item.url = '/err-404'
    }
  });
  // 
  routerBox.push(...nextRouter);
  store.dispatch('menu/setUserMenuList', menu);
  // err404
  let errorBox = {
    path: "*",
    redirect: "/err-404"
  };
  userRouter.children = routerBox;
  router.addRoutes([userRouter, errorBox]);
}

export default addUserRouter