// 
import router from "@/router";
import store from "@/store";
import { getMenuDataApi } from "@/api/menu"; // 
import { localGet, localDel } from "@/util/storage"; // 
import { isLoginExpired, monitoringVisibilityChange } from './auxiliary'; // jwt,tab
import addUserRouter from './add-routes'; // 
import global from '@/global/index';
import {
  getUnitApi,
} from "@/api/roomDataAPI";
const project_key = global.project_key;
// 
function registerRouteGuard() {
  router.beforeEach((to, from, next) => {
    // 
    let local_auth = localGet(project_key, true);
    // 
    if (local_auth && local_auth != 'undefined') {
      // token
      if (isLoginExpired(local_auth)) {
        store.dispatch('app/setToken', '')
        localDel(project_key);
        next({
          path: "/login"
        });
        return;
      }
      // 
      store.dispatch('app/setToken', local_auth)
      // 
      if (store.getters.menu.length === 0) {
        getMenuDataApi()
          .then(({ data }) => {
            let _menu = data.data || [];
            // 
           console.log(_menu);
            if(localGet("role")==2)
            {
              _menu[0].children=[];

              _menu[0].children.push( 
                {
                  id: "3-6",
                  title: "",
                  url: "/ui/unit"
                },
                {
                id: "3-28",
                title: "",
                url: "/ui/weixiu"
              }
              );

            }
            else{
              
              _menu[0].children.push( {
                id: "3-28",
                title: "",
                url: "/ui/weixiu"
              });
              _menu[0].children.push( {
                id: "3-29",
                title: "",
                url: "/ui/weixiuhetong"
              });

            }
            
            addUserRouter(_menu);
            // 
            store.dispatch('menu/setUserMenu', _menu)

            getUnitApi().then((test)=>{
              if(test.data.code==2)
              {
                store.dispatch('app/setToken', '')
                localDel(project_key);
                next({
                  path: "/login"
                });
                return;
              }
              
            });
            next({ ...to, replace: true });
          })
          .catch();
      } else {
        // 
        if (to.path == "/login") {
          next({
            name: "index"
          });
        } else {
          next();
        }
      }
    } else {
      //  
      if (to.path == "/login" || to.meta.true) {
        next();
      } else {
        next({
          path: "/login"
        });
      }
    }
  });
}
// 
function AUTH() {
  monitoringVisibilityChange();
  registerRouteGuard();
}

export default AUTH