export default {
  namespaced: true,
  state: {
    size: 10,  //  
    token: '', // token
  },
  mutations: {
    // 
    SET_PAGE_SIZE(state, data) {
      state.size = data;
    },
    // token
    SET_TOKEN(state, data) {
      state.token = data;
    },
  },
  actions: {
    // 
    setPageSize({ commit }, data) {
      commit('SET_PAGE_SIZE', data)
    },
    // token
    setToken({ commit }, data) {
      commit('SET_TOKEN', data)
    }
  }
}
